.form-leaving {
  z-index: 30;
  color: #0e0d17;
  background: #fff;
  flex-direction: column;
  justify-content: center;
  gap: 15vw;
  width: 100%;
  height: 100%;
  transition: right .6s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  right: -100%;
}

.form-leaving.show {
  right: 0;
}

.form-leaving .header {
  justify-content: space-between;
  align-items: flex-start;
  display: flex;
}

.form-leaving .header .title {
  color: #0e0d17;
  letter-spacing: -.45vw;
  font-size: 7.5vw;
  font-weight: 500;
  line-height: 110%;
}

.form-leaving .header .close-button {
  background-image: url("close-black.b9f3d96f.svg");
  background-repeat: no-repeat;
  width: 10vw;
  height: 10vw;
}

.form-leaving form {
  flex-direction: column;
  align-items: flex-start;
  gap: 6.6vw;
  display: flex;
}

.form-leaving form .title {
  letter-spacing: -.53vw;
  font-size: 8.75vw;
  font-style: normal;
  font-weight: 500;
  line-height: 110%;
}

.form-leaving form .inputs-container {
  flex-direction: column;
  align-self: stretch;
  gap: 2.7vw;
  display: flex;
}

.form-leaving form .inputs-container input {
  letter-spacing: -.2vw;
  border: 1px solid #d9d9d9;
  border-radius: 5vw;
  align-self: stretch;
  padding: 3.44vw 3.75vw;
  font-size: 5vw;
  line-height: 120%;
}

.form-leaving form .inputs-container input::placeholder {
  opacity: .6;
}

.form-leaving form .form-footer {
  align-self: stretch;
  align-items: center;
  gap: 2.5vw;
  display: flex;
}

.form-leaving form .form-footer .submit {
  color: #fff;
  letter-spacing: -.09vw;
  background: #0e0d17;
  border-radius: 4.38vw;
  align-items: center;
  gap: 2.5vw;
  padding: 3.13vw 6.25vw;
  font-size: 4.38vw;
  line-height: 100%;
  display: flex;
}

.form-leaving form .form-footer p {
  letter-spacing: -.06vw;
  font-size: 2.81vw;
  line-height: 110%;
}

.form-leaving form .form-footer p a {
  text-underline-offset: 3px;
  text-decoration-line: underline;
}

@media (width >= 650px) {
  .form-leaving {
    z-index: 30;
    color: #0e0d17;
    background: #fff;
    flex-direction: column;
    justify-content: center;
    gap: 15vw;
    width: 100%;
    height: 100%;
    transition: right .6s ease-in-out;
    display: flex;
    position: fixed;
    top: 0;
    right: -100%;
  }

  .form-leaving.show {
    right: 0;
  }
}
